            { name: lessonById[1].lessonName + ' (Консультація)', link: lessonById[1].link },
            { name: lessonById[0].lessonName + ' (Практика)', link: lessonById[0].link }
          ],
        };


  export default {
    data() {
      return {
        selectedOption: 'AT212',
        showContent: null,
      }
    },

    components: {
      LiveSchedule,
      ScheduleAT211,
      ScheduleAT212,
    },
    computed: {
      thisWeekSteamy() {
        if(this.getWeekNumber%2==0) 
          return 1;
        else
          return null;
      },
      getUnpairWeekScheduleAT212(){
        return scheduleAT212;
      },
      getSteamyWeekScheduleAT212(){
        let steamyWeekScheduleAT212 = JSON.parse(JSON.stringify(scheduleAT212));
  
        steamyWeekScheduleAT212["Вівторок"][1].name=lessonById[2].lessonName;
        steamyWeekScheduleAT212["Вівторок"][1].link=lessonById[2].link;

        steamyWeekScheduleAT212["Середа"][0].name="Відпочиваємо";
        steamyWeekScheduleAT212["Середа"][0].link=null;

        steamyWeekScheduleAT212["Четвер"][2].name=lessonById[6].lessonName + ' (Практика)';
        steamyWeekScheduleAT212["Четвер"][2].link=lessonById[6].link;
        return steamyWeekScheduleAT212;
      },
      getWeekNumber(){
        const today = new Date();
        const startOfYear = new Date(today.getFullYear(), 0, 1);
        const weekNumber = Math.ceil(((today - startOfYear) / 86400000 + startOfYear.getDay() + 1) / 7)-9;
        return weekNumber;
      },
      getDay(){
        const daysOfWeek = ['неділя', 'понеділок', 'вівторок', 'середа', 'четвер', 'п`ятниця', 'субота'];
        const date = new Date();
        const currentDayOfWeek = daysOfWeek[date.getDay()];
        return currentDayOfWeek
      }
      
    },
    mounted() { 
     
    },
    methods: {
      
    },
   
};
