<template>
  <div v-if=" this.group == 'AT212'" class="scheduleContainer">
    <!-- Раздел для сегодняшнего расписания -->
    <div  class="fast-travel-container">
      <div class="fast-travel-title-container">
        <span class="fast-travel-title">{{ this.whenPairWord }}</span>
        <span class="fast-pair-name"> {{ this.nameCurrentPair }}</span>
      </div>
      <div class="fast-travel-button-container">
        <span class="pair-start-time">{{ this.startPairTime }}</span>
          <button class="fast-travel-button" > <a class="fast-pair-link" :href="this.linkCurrentPair">{{ this.coupleButton }}</a>   </button>
        <span class="pair-end-time">{{ this.endPairTime }}</span>
      </div>
    </div>
    <div class="scheduleElement" v-if="todaysSchedule.today.length">
      <h2>Розклад на сьогодні:</h2>
      <ul>
        <li v-for="lesson in todaysSchedule.today" :key="lesson.name">      
          <a v-if="lesson.link != null" :href="lesson.link">{{ lesson.name }}</a>
          <a v-else>{{ lesson.name }}</a>
        </li>
      </ul>
    </div>
    <div class="scheduleElement" v-else>
      <h2>Сьогодні вихідний:</h2>
      <ul>
        <li class="fieldNoPairs">      
          <a >Пар немає</a>
        </li>
      </ul>
    </div>
    <!-- Раздел для расписания на завтра -->
    <div class="scheduleElement" v-if="todaysSchedule.tomorrow.length">
      <h2>Розклад на завтра:</h2>
      <ul>
        <li v-for="lesson in todaysSchedule.tomorrow" :key="lesson.name">      
          <a>{{ lesson.name }}</a>
        </li>
      </ul>
    </div>
    <div class="scheduleElement" v-else>
      <h2>Завтра вихідний</h2>
      <ul>
        <li class="fieldNoPairs">      
          <a >Пар немає</a>
        </li>
      </ul>
    </div>
  </div>
  <div v-else>
    <!-- Add content for other groups if needed -->
  </div>
</template>

<script>
  const timezoneOffsetDiff = new Date().getTimezoneOffset() / 60;

  const pairs = {
    "0": {
      "pairStart": `${11 + timezoneOffsetDiff}:00`,
      "pairEnd": `${12 + timezoneOffsetDiff}:35`
    },
    "1": {
      "pairStart": `${12 + timezoneOffsetDiff}:50`,
      "pairEnd": `${14 + timezoneOffsetDiff}:25`
    },
    "2": {
      "pairStart": `${14 + timezoneOffsetDiff}:40`,
      "pairEnd": `${16 + timezoneOffsetDiff}:15`
    },
    "3": {
      "pairStart": `${16 + timezoneOffsetDiff}:30`,
      "pairEnd": `${18 + timezoneOffsetDiff}:05`
    }
  };

  const pairEndTimes = [
    new Date().setHours(12+timezoneOffsetDiff, 35, 0, 0),
    new Date().setHours(14+timezoneOffsetDiff, 25, 0, 0),
    new Date().setHours(24+timezoneOffsetDiff, 15, 0, 0),
    new Date().setHours(18+timezoneOffsetDiff, 5, 0, 0)
  ];
 
export default {
  data() {
    return {
      linkCurrentPair: null,
      nameCurrentPair: null,
      whenPairWord: "Пари закінчилися",
      coupleButton: "Посилання немає",
      startPairTime: "",
      endPairTime: "",
    };
  },
  props: ['group', 'thisWeekSteamy','unpairWeekScheduleAT212','steamyWeekScheduleAT212'],
 
  mounted(){
    this.updateLinkAndName();

    console.log(timezoneOffsetDiff); 
    setInterval(() => {
      this.updateLinkAndName();
    }, 60000);
  },

  methods: {
    updateLinkAndName() {
    const pairNumber = this.getPairNumber();

    if (pairNumber !== null) {
      const currentPair = this.todaysSchedule.today[pairNumber];
      this.nameCurrentPair = currentPair.name;
      this.whenPairWord = this.updateWhenPairWord();
      this.startPairTime = pairs[pairNumber].pairStart;
      this.endPairTime = pairs[pairNumber].pairEnd;

      this.coupleButton = currentPair.link ? "Перейти на пару" : "Посилання немає";
    } else {
      this.nameCurrentPair = null;
      this.whenPairWord = this.updateWhenPairWord();
    }

    this.linkCurrentPair = pairNumber !== null && this.todaysSchedule?.today?.[pairNumber]?.link
      ? this.todaysSchedule.today[pairNumber].link
      : null;
    },
    updateWhenPairWord(){
      const now = Date.now();
      if(now > pairEndTimes[this.todaysSchedule.today.length]){
        return "Пари закінчилися";
      }else if(now < new Date().setHours(12+timezoneOffsetDiff, 0, 0, 0)){
        return "Далі:";
      }else{
        for (let i = 0; i < this.todaysSchedule.today.length; i++) {
          if (now > pairEndTimes[i] && now < new Date(pairEndTimes[i] + 15*60000)) {
            return "Далі:";
          }
          return "Зараз:";
        }
      }
    },
    getPairNumber() {
    const now = Date.now();
    for (let i = 0; i < pairEndTimes.length && i < this.todaysSchedule.today.length; i++) {
      if (now <= pairEndTimes[i]) {
        return i;
      }
    }
    return null;
    },
    
  },

  computed: {
  todaysSchedule() {
    const daysOfWeek = ["Неділя", "Понеділок", "Вівторок", "Середа", "Четвер", "П`ятниця", "Суббота"];
    const now = new Date();
    const dayOfWeek = now.getDay();

    const schedule = { today: [], tomorrow: [] };

    if (this.group === 'AT212') {
      const weekSchedule = this.thisWeekSteamy ? this.steamyWeekScheduleAT212 : this.unpairWeekScheduleAT212;

      schedule.today = weekSchedule[daysOfWeek[dayOfWeek]] ?? [];
      schedule.tomorrow = weekSchedule[daysOfWeek[dayOfWeek + 1]] ?? [];
    }
    return schedule;
  },
}

};
</script>